// src/pages/api/user/has-inscription.ts
import type { NextApiRequest, NextApiResponse } from "next";
import { getServerSession } from "next-auth/next";
import { authOptions } from "./authOptions"; 
import prisma from "@/lib/prisma";

export default async function handler(req: NextApiRequest, res: NextApiResponse) {
  const session = await getServerSession(req, res, authOptions);
  if (!session?.user?.id) return res.status(401).json({ hasInscription: false });
  const count = await prisma.userPackage.count({
    where: {
      userId: session.user.id,
      pkg: { name: "Inscripción Bloom" },
    },
  });
  res.json({ hasInscription: count > 0 });
  // 3. Si no tiene inscripción, redirigir a /dashboard?tab=mis-paquetes
  if (req.method === "GET") {
    if (!session?.user?.id) {
      return res.redirect("/dashboard?tab=mis-paquetes");
    }
  }

  if (req.method !== "GET") {
    res.setHeader("Allow", "GET");
    return res.status(405).end("Method not allowed");
  }
}